@media only screen and (max-width: 991px) {

    #ctaSection {
        margin-top: 60px;
    }

    #ctaContent {
        background-color: #F1632D;
        width: 430px;
        height: 145px;
        display: flex;
        justify-content: center;
        position: relative;
        right: 20px;
        gap: 10px;
    }

    #ctaHeader {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    #ctaHeading {
        font-family: "Poppins", sans-serif;
        font-size: 12px;
        font-weight: 600;
        line-height: 21.5px;
        text-align: center;
        align-items: center;
        margin: 0;
        color: #FFFFFF;
    }

    #ctaDescription {
        font-family: "Poppins", sans-serif;
        font-size: 6px;
        line-height: 24px;
        text-align: center;
        align-items: center;
        color: #FFFFFF;
    }

    #emailFormContainer {
        display: flex;
        width: 105px;
        height: 20px;
    }

    #emailInput {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 105px;
        height: 20px;
        border: 0;
        border-radius: 2px;
        padding: 0px;
        padding-left: 7px;
        font-size: 6px;
        text-align: left;
        align-items: center;
    }

    #emailInput::placeholder {
        font-size: 6px;
        text-align: left;
        align-items: center;
    }

    #subscribeButton {
        position: relative;
        border: 0px;
        border-radius: 2px;
        background-color: #F1632D;
        width: 12px;
        height: 12px;
    }

    .bi-arrow-right-square {
        font-size: 5px;
        color: #FFFFFF;
    }

    #ctaImageContainer {
        display: flex;
        justify-content: center;
        padding: 8px;
        width: 198px;
        height: 240px;
        border-radius: 8px;
        background-color: #FFFFFF;
    }

    #ctaImage {
        height: 224px;
        width: 182px;
        background-color: #E2E2E2;
        border-radius: 8px;
    }

}