.carousel-custom {
  --carousel-height: 400px;
  --carousel-width: 1000px;
  --carousel-item-height: 150px;
  --carousel-item-width: 150px;
  width: 100%;
}

.carousel-custom-container {
  min-height: var(--carousel-height);
  max-width: var(--carousel-width);
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: relative;
}

@media only screen and (width >= 991px) {
  .carousel-custom-item {
    height: var(--carousel-item-height);
    opacity: 0;
    width: var(--carousel-item-width);
    z-index: 0;
    transition: all .3s ease-in-out;
    position: absolute;
    transform: translateX(-50%);
  }

  .carousel-custom-item-1 {
    opacity: .4;
    left: 15%;
  }

  .carousel-custom-item-2, .carousel-custom-item-4 {
    height: calc(var(--carousel-item-height) * 1.5);
    opacity: 1;
    width: calc(var(--carousel-item-width) * 1.5);
    z-index: 1;
  }

  .carousel-custom-item-2 {
    left: 30%;
  }

  .carousel-custom-item-3 {
    height: calc(var(--carousel-item-height) * 2);
    opacity: 1;
    width: calc(var(--carousel-item-width) * 2);
    z-index: 2;
    left: 50%;
    box-shadow: 0 0 30px #fff9, 0 0 60px #ffffff73, 0 0 110px #ffffff40, 0 0 100px #ffffff1a;
  }

  .carousel-custom-item-4 {
    left: 70%;
  }

  .carousel-custom-item-5 {
    opacity: .4;
    left: 85%;
  }
}

.carousel-custom-controls {
  z-index: 10;
  justify-content: space-between;
  display: flex;
  position: relative;
}

.carousel-custom-control {
  color: #aaa;
  cursor: pointer;
  background-color: #0000;
  border: 2px solid;
  border-radius: 4px;
  width: 22px;
  height: 22px;
  margin: 0 20px;
  transition: transform .5s ease-out;
  position: relative;
  transform: scale(1.5);
}

.carousel-custom-control:hover {
  transform: scale(1.3);
}

.carousel-custom-control-previous:after, .carousel-custom-control-previous:before {
  box-sizing: border-box;
  content: "";
  height: 8px;
  display: block;
  position: absolute;
  top: 5px;
}

.carousel-custom-control-previous:before {
  background: currentColor;
  border-radius: 2px;
  width: 2px;
  right: 11px;
}

.carousel-custom-control-previous:after {
  border-top: 4px solid #0000;
  border-bottom: 4px solid #0000;
  border-right: 5px solid;
  width: 0;
  right: 5px;
}

.carousel-custom-control-next:after, .carousel-custom-control-next:before {
  box-sizing: border-box;
  content: "";
  height: 8px;
  display: block;
  position: absolute;
  top: 5px;
}

.carousel-custom-control-next:before {
  background: currentColor;
  border-radius: 2px;
  width: 2px;
  left: 11px;
}

.carousel-custom-control-next:after {
  border-top: 4px solid #0000;
  border-bottom: 4px solid #0000;
  border-left: 5px solid;
  width: 0;
  left: 5px;
}

.carousel-custom-control-play:before {
  box-sizing: border-box;
  content: "";
  border-top: 5px solid #0000;
  border-bottom: 5px solid #0000;
  border-left: 6px solid;
  width: 0;
  height: 10px;
  display: block;
  position: absolute;
  top: 4px;
  left: 7px;
}

.carousel-custom-control-play.playing:before {
  box-sizing: border-box;
  content: "";
  border: 0;
  border-left: 2px solid;
  border-right: 2px solid;
  width: 6px;
  height: 6px;
  display: block;
  position: absolute;
  top: 6px;
  left: 6px;
}

.carousel-custom-control-add:after, .carousel-custom-control-add:before {
  box-sizing: border-box;
  content: "";
  background: currentColor;
  border-radius: 5px;
  width: 10px;
  height: 2px;
  display: block;
  position: absolute;
  top: 8px;
  left: 4px;
}

.carousel-custom-control-add:after {
  width: 2px;
  height: 10px;
  top: 4px;
  left: 8px;
}

